  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "montserrat", sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500; }

.loading {
  width: 200px;
  height: 200px;
  box-sizing: border-box;
  border-radius: 50%;
  border-top: 10px solid #2a2a2a;
  position: relative;
  animation: a1 2s linear infinite; }

.loading::before,
.loading::after {
  content: "";
  width: 200px;
  height: 200px;
  position: absolute;
  left: 0;
  top: -10px;
  box-sizing: border-box;
  border-radius: 50%; }

.loading::before {
  border-top: 10px solid #01aef0;
  transform: rotate(120deg); }

.loading::after {
  border-top: 10px solid #72b645;
  transform: rotate(240deg); }

.loading span {
  position: absolute;
  width: 200px;
  height: 200px;
  color: #fff;
  text-align: center;
  line-height: 200px;
  animation: a2 2s linear infinite; }

@keyframes a1 {
  to {
    transform: rotate(360deg); } }
@keyframes a2 {
  to {
    transform: rotate(-360deg); } }
